import { useState, useEffect } from 'react'
import clsx from 'clsx'
import { useAgency } from '../contexts/AgencyContext'
import {
  AreaChart, Area, BarChart, Bar, LineChart, Line,
  XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer,
  PieChart, Pie, Cell, Legend
} from 'recharts'

interface KPIData {
  onTimePerformance: number
  activeRoutes: number
  totalDepartures: number
  estimatedRevenue: number
  activeAlerts: number
  avgDelay: number
}

interface RouteHealth {
  route: string
  route_id?: string
  onTime: number
  reliability: number
  utilization: number
  status: 'healthy' | 'warning' | 'critical'
  agency?: string
  city?: string
}

function KPICard({ title, value, unit, severity, icon }: {
  title: string
  value: number | string
  unit?: string
  severity: 'success' | 'warning' | 'danger' | 'info'
  icon: React.ReactNode
}) {
  const severityColors = {
    success: 'border-l-transit-500 bg-transit-500/10',
    warning: 'border-l-severity-warning bg-severity-warning/10',
    danger: 'border-l-severity-danger bg-severity-danger/10',
    info: 'border-l-severity-info bg-severity-info/10',
  }

  const iconColors = {
    success: 'text-transit-500',
    warning: 'text-severity-warning',
    danger: 'text-severity-danger',
    info: 'text-severity-info',
  }

  return (
    <div className={clsx(
      'relative p-5 rounded-xl bg-dark-surface border-l-4 card-hover',
      severityColors[severity]
    )}>
      <div className="flex items-start justify-between">
        <div>
          <p className="text-sm text-dark-muted mb-1">{title}</p>
          <p className="text-3xl font-bold text-white">
            {typeof value === 'number' ? value.toLocaleString() : value}
            {unit && <span className="text-lg font-normal text-dark-muted ml-1">{unit}</span>}
          </p>
        </div>
        <div className={clsx('p-3 rounded-lg bg-dark-bg', iconColors[severity])}>
          {icon}
        </div>
      </div>
    </div>
  )
}

function SeverityBadge({ status }: { status: 'healthy' | 'warning' | 'critical' }) {
  const config = {
    healthy: { color: 'bg-transit-500', text: 'Healthy', glow: 'glow-green' },
    warning: { color: 'bg-severity-warning', text: 'Warning', glow: 'glow-yellow' },
    critical: { color: 'bg-severity-danger', text: 'Critical', glow: 'glow-red' },
  }
  const { color, text, glow } = config[status]

  return (
    <span className={clsx('inline-flex items-center gap-1.5 px-2.5 py-1 rounded-full text-xs font-medium', color, 'text-white', glow)}>
      <span className="w-1.5 h-1.5 rounded-full bg-white severity-pulse"></span>
      {text}
    </span>
  )
}

export default function Dashboard() {
  const { agency } = useAgency()
  const [kpiData, setKpiData] = useState<KPIData | null>(null)
  const [routeHealth, setRouteHealth] = useState<RouteHealth[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [cityInfo, setCityInfo] = useState<string>('')

  useEffect(() => {
    fetchDashboardData()
  }, [agency])

  const fetchDashboardData = async () => {
    setLoading(true)
    setError(null)
    
    try {
      // Fetch KPIs
      const kpiUrl = agency === 'All' 
        ? 'http://localhost:8000/api/kpis'
        : `http://localhost:8000/api/kpis?agency=${encodeURIComponent(agency)}`
      const kpiResponse = await fetch(kpiUrl)
      const kpiResult = await kpiResponse.json()
      
      if (kpiResult.success) {
        setKpiData(kpiResult.data)
      } else {
        setError(kpiResult.error || 'Failed to fetch KPIs')
      }

      // Fetch Route Health
      const healthUrl = agency === 'All'
        ? 'http://localhost:8000/api/analytics/route-health'
        : `http://localhost:8000/api/analytics/route-health?agency=${agency}`
      const healthResponse = await fetch(healthUrl)
      const healthResult = await healthResponse.json()
      
      if (healthResult.success) {
        const routes = healthResult.data || []
        setRouteHealth(routes)
        
        // Extract city information
        if (routes.length > 0) {
          const cities = [...new Set(routes.map((r: RouteHealth) => r.city).filter(Boolean))]
          setCityInfo(cities.join(', ') || 'Unknown')
        }
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to fetch data')
    } finally {
      setLoading(false)
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-transit-500 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-dark-muted">Loading dashboard data...</p>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="p-6 bg-severity-danger/10 border border-severity-danger rounded-xl">
        <p className="text-severity-danger">Error: {error}</p>
        <button 
          onClick={fetchDashboardData}
          className="mt-4 px-4 py-2 bg-transit-500 text-white rounded hover:bg-transit-600"
        >
          Retry
        </button>
      </div>
    )
  }

  if (!kpiData) {
    return (
      <div className="p-6 bg-dark-surface border border-dark-border rounded-xl">
        <p className="text-dark-muted">No data available for {agency === 'All' ? 'selected agencies' : agency}</p>
      </div>
    )
  }

  // Prepare chart data
  // Top routes by on-time performance (for bar chart)
  const topRoutesChart = routeHealth
    .slice(0, 10)
    .map(r => ({
      name: r.route.length > 20 ? r.route.substring(0, 20) + '...' : r.route,
      onTime: r.onTime,
      reliability: r.reliability,
      utilization: r.utilization
    }))

  // Performance distribution by status
  const statusCounts = {
    healthy: routeHealth.filter(r => r.status === 'healthy').length,
    warning: routeHealth.filter(r => r.status === 'warning').length,
    critical: routeHealth.filter(r => r.status === 'critical').length,
  }

  // Hourly demand simulation (based on departures - would be real data in production)
  const hourlyDemand = Array.from({ length: 24 }, (_, i) => ({
    hour: `${i}:00`,
    departures: Math.floor(Math.random() * 50) + 20, // Placeholder - would be real data
    peak: (i >= 7 && i <= 9) || (i >= 17 && i <= 19) ? 'peak' : 'off-peak'
  }))

  // Calculate pie chart data
  const onTimeCount = routeHealth.filter(r => r.onTime >= 90).length
  const lateCount = routeHealth.filter(r => r.onTime >= 75 && r.onTime < 90).length
  const veryLateCount = routeHealth.filter(r => r.onTime < 75).length
  const pieData = [
    { name: 'On-Time', value: onTimeCount, color: '#3FB950' },
    { name: 'Late', value: lateCount, color: '#D29922' },
    { name: 'Very Late', value: veryLateCount, color: '#F85149' },
  ].filter(item => item.value > 0)

  return (
    <div className="space-y-6">
      {/* Agency & City Indicator */}
      <div className="p-4 rounded-xl bg-dark-surface border border-dark-border">
        <div className="flex items-center justify-between">
          <div>
            <p className="text-sm text-dark-muted">
              Viewing data for: <span className="text-white font-semibold">{agency === 'All' ? 'All Agencies' : agency}</span>
            </p>
            {cityInfo && (
              <p className="text-sm text-dark-muted mt-1">
                Location: <span className="text-white font-semibold">{cityInfo}</span>
              </p>
            )}
          </div>
        </div>
      </div>

      {/* KPI Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 xl:grid-cols-5 gap-4">
        <KPICard
          title="On-Time Performance"
          value={kpiData.onTimePerformance}
          unit="%"
          severity="success"
          icon={<svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" /></svg>}
        />
        <KPICard
          title="Active Routes"
          value={kpiData.activeRoutes}
          severity="info"
          icon={<svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 20l-5.447-2.724A1 1 0 013 16.382V5.618a1 1 0 011.447-.894L9 7m0 13l6-3m-6 3V7m6 10l4.553 2.276A1 1 0 0021 18.382V7.618a1 1 0 00-.553-.894L15 4m0 13V4m0 0L9 7" /></svg>}
        />
        <KPICard
          title="Total Departures"
          value={kpiData.totalDepartures}
          severity="info"
          icon={<svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" /></svg>}
        />
        <KPICard
          title="Active Alerts"
          value={kpiData.activeAlerts}
          severity="warning"
          icon={<svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" /></svg>}
        />
        <KPICard
          title="Avg Delay"
          value={`${kpiData.avgDelay.toFixed(1)}`}
          unit="min"
          severity={kpiData.avgDelay > 5 ? 'danger' : kpiData.avgDelay > 2 ? 'warning' : 'success'}
          // description={kpiData.avgDelay > 0 ? "Average delay for delayed departures only" : "No delays recorded"}
          icon={<svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" /></svg>}
        />
      </div>

      {/* Charts Row */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* On-Time Performance by Route */}
        <div className="p-6 rounded-xl bg-dark-surface border border-dark-border">
          <div className="flex items-center justify-between mb-4">
            <div>
              <h3 className="text-lg font-semibold text-white">On-Time Performance by Route</h3>
              <p className="text-sm text-dark-muted">Top 10 routes</p>
            </div>
          </div>
          {topRoutesChart.length > 0 ? (
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={topRoutesChart}>
                <CartesianGrid strokeDasharray="3 3" stroke="#30363D" />
                <XAxis dataKey="name" stroke="#8B949E" fontSize={10} angle={-45} textAnchor="end" height={80} />
                <YAxis stroke="#8B949E" fontSize={12} domain={[0, 100]} />
                <Tooltip
                  contentStyle={{ background: '#0D1117', border: '1px solid #3FB950', borderRadius: '8px', color: '#FFFFFF' }}
                  labelStyle={{ color: '#FFFFFF', fontWeight: 'bold' }}
                  itemStyle={{ color: '#C9D1D9' }}
                />
                <Bar dataKey="onTime" name="On-Time %" radius={[4, 4, 0, 0]}>
                  {topRoutesChart.map((entry, index) => (
                    <Cell 
                      key={`cell-${index}`} 
                      fill={entry.onTime >= 90 ? '#3FB950' : entry.onTime >= 75 ? '#D29922' : '#F85149'} 
                    />
                  ))}
                </Bar>
              </BarChart>
            </ResponsiveContainer>
          ) : (
            <div className="text-center py-8 text-dark-muted">No route data available</div>
          )}
        </div>

        {/* Hourly Demand Pattern */}
        <div className="p-6 rounded-xl bg-dark-surface border border-dark-border">
          <div className="flex items-center justify-between mb-4">
            <div>
              <h3 className="text-lg font-semibold text-white">Hourly Departure Demand</h3>
              <p className="text-sm text-dark-muted">Service frequency by hour</p>
            </div>
          </div>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={hourlyDemand}>
              <CartesianGrid strokeDasharray="3 3" stroke="#30363D" />
              <XAxis dataKey="hour" stroke="#8B949E" fontSize={10} />
              <YAxis stroke="#8B949E" fontSize={12} />
              <Tooltip
                contentStyle={{ background: '#0D1117', border: '1px solid #3FB950', borderRadius: '8px', color: '#FFFFFF' }}
                labelStyle={{ color: '#FFFFFF', fontWeight: 'bold' }}
                itemStyle={{ color: '#C9D1D9' }}
              />
              <Bar dataKey="departures" name="Departures" radius={[4, 4, 0, 0]}>
                {hourlyDemand.map((entry, index) => (
                  <Cell 
                    key={`cell-${index}`} 
                    fill={entry.peak === 'peak' ? '#D29922' : '#3FB950'} 
                  />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* Route Health Overview - Chart View */}
      <div className="p-6 rounded-xl bg-dark-surface border border-dark-border">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h3 className="text-lg font-semibold text-white">Route Health Overview</h3>
            <p className="text-sm text-dark-muted">Status by route for {agency === 'All' ? 'all agencies' : agency} in {cityInfo || 'Unknown'}</p>
          </div>
        </div>
        {routeHealth.length === 0 ? (
          <div className="text-center py-8 text-dark-muted">
            No route health data available
          </div>
        ) : (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Reliability vs Utilization Scatter */}
            <div>
              <h4 className="text-sm font-medium text-white mb-3">Reliability vs Utilization</h4>
              <ResponsiveContainer width="100%" height={250}>
                <LineChart data={routeHealth.slice(0, 15)}>
                  <CartesianGrid strokeDasharray="3 3" stroke="#30363D" />
                  <XAxis dataKey="reliability" stroke="#8B949E" fontSize={10} label={{ value: 'Reliability', position: 'insideBottom', offset: -5, fill: '#8B949E' }} />
                  <YAxis stroke="#8B949E" fontSize={10} label={{ value: 'Utilization', angle: -90, position: 'insideLeft', fill: '#8B949E' }} />
                  <Tooltip
                    contentStyle={{ background: '#0D1117', border: '1px solid #3FB950', borderRadius: '8px', color: '#FFFFFF' }}
                    labelStyle={{ color: '#FFFFFF', fontWeight: 'bold' }}
                    itemStyle={{ color: '#C9D1D9' }}
                    formatter={(value: any, name: string) => [value, name]}
                    labelFormatter={(label) => `Route: ${label}`}
                  />
                  <Line type="monotone" dataKey="utilization" stroke="#3FB950" strokeWidth={2} dot={{ fill: '#3FB950', r: 4 }} />
                </LineChart>
              </ResponsiveContainer>
            </div>

            {/* Status Distribution */}
            <div>
              <h4 className="text-sm font-medium text-white mb-3">Route Status Distribution</h4>
              <ResponsiveContainer width="100%" height={250}>
                <PieChart>
                  <Pie
                    data={[
                      { name: 'Healthy', value: statusCounts.healthy, color: '#3FB950' },
                      { name: 'Warning', value: statusCounts.warning, color: '#D29922' },
                      { name: 'Critical', value: statusCounts.critical, color: '#F85149' },
                    ].filter(item => item.value > 0)}
                    cx="50%"
                    cy="50%"
                    innerRadius={60}
                    outerRadius={80}
                    paddingAngle={5}
                    dataKey="value"
                  >
                    {[
                      { name: 'Healthy', value: statusCounts.healthy, color: '#3FB950' },
                      { name: 'Warning', value: statusCounts.warning, color: '#D29922' },
                      { name: 'Critical', value: statusCounts.critical, color: '#F85149' },
                    ].filter(item => item.value > 0).map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <Tooltip
                    contentStyle={{ background: '#0D1117', border: '1px solid #3FB950', borderRadius: '8px', color: '#FFFFFF' }}
                    labelStyle={{ color: '#FFFFFF', fontWeight: 'bold' }}
                    itemStyle={{ color: '#C9D1D9' }}
                  />
                  <Legend />
                </PieChart>
              </ResponsiveContainer>
            </div>
          </div>
        )}
      </div>

      {/* Additional Charts Row */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Performance Distribution Pie */}
        {pieData.length > 0 && (
          <div className="p-6 rounded-xl bg-dark-surface border border-dark-border">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-white">Performance Distribution</h3>
            </div>
            <ResponsiveContainer width="100%" height={220}>
              <PieChart>
                <Pie
                  data={pieData}
                  cx="50%"
                  cy="50%"
                  innerRadius={60}
                  outerRadius={80}
                  paddingAngle={5}
                  dataKey="value"
                >
                  {pieData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip
                  contentStyle={{ background: '#0D1117', border: '1px solid #3FB950', borderRadius: '8px', color: '#FFFFFF' }}
                  labelStyle={{ color: '#FFFFFF', fontWeight: 'bold' }}
                  itemStyle={{ color: '#C9D1D9' }}
                />
              </PieChart>
            </ResponsiveContainer>
            <div className="flex justify-center gap-4 mt-2">
              {pieData.map((item) => (
                <div key={item.name} className="flex items-center gap-2 text-sm">
                  <span className="w-3 h-3 rounded-full" style={{ backgroundColor: item.color }}></span>
                  <span className="text-dark-muted">{item.name}: {item.value}</span>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Utilization Distribution */}
        {routeHealth.length > 0 && (
          <div className="p-6 rounded-xl bg-dark-surface border border-dark-border">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-white">Utilization Distribution</h3>
            </div>
            <ResponsiveContainer width="100%" height={220}>
              <BarChart data={routeHealth.slice(0, 10).map(r => ({ name: r.route.length > 15 ? r.route.substring(0, 15) + '...' : r.route, utilization: r.utilization }))}>
                <CartesianGrid strokeDasharray="3 3" stroke="#30363D" />
                <XAxis dataKey="name" stroke="#8B949E" fontSize={9} angle={-45} textAnchor="end" height={60} />
                <YAxis stroke="#8B949E" domain={[0, 100]} />
                <Tooltip
                  contentStyle={{ background: '#0D1117', border: '1px solid #3FB950', borderRadius: '8px', color: '#FFFFFF' }}
                  labelStyle={{ color: '#FFFFFF', fontWeight: 'bold' }}
                  itemStyle={{ color: '#C9D1D9' }}
                />
                <Bar dataKey="utilization" name="Utilization %" radius={[4, 4, 0, 0]}>
                  {routeHealth.slice(0, 10).map((entry, index) => (
                    <Cell 
                      key={`cell-${index}`} 
                      fill={entry.utilization >= 70 ? '#3FB950' : entry.utilization >= 40 ? '#D29922' : '#8B949E'} 
                    />
                  ))}
                </Bar>
              </BarChart>
            </ResponsiveContainer>
          </div>
        )}
      </div>

      {/* Route Performance Trend */}
      {routeHealth.length > 0 && (
        <div className="p-6 rounded-xl bg-dark-surface border border-dark-border">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-white">Route Performance Comparison</h3>
            <p className="text-sm text-dark-muted">Top 15 routes</p>
          </div>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={routeHealth.slice(0, 15)}>
              <CartesianGrid strokeDasharray="3 3" stroke="#30363D" />
              <XAxis dataKey="route" stroke="#8B949E" fontSize={9} angle={-45} textAnchor="end" height={80} />
              <YAxis stroke="#8B949E" />
              <Tooltip
                contentStyle={{ background: '#0D1117', border: '1px solid #3FB950', borderRadius: '8px', color: '#FFFFFF' }}
                labelStyle={{ color: '#FFFFFF', fontWeight: 'bold' }}
                itemStyle={{ color: '#C9D1D9' }}
              />
              <Bar dataKey="onTime" name="On-Time %" fill="#3FB950" radius={[4, 4, 0, 0]} />
              <Bar dataKey="reliability" name="Reliability %" fill="#58A6FF" radius={[4, 4, 0, 0]} />
            </BarChart>
          </ResponsiveContainer>
        </div>
      )}

      {/* Footer with Developer & Data Source Info */}
      <div className="mt-8 p-4 rounded-xl bg-dark-surface border border-dark-border">
        <div className="flex flex-col md:flex-row items-center justify-between gap-4">
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-2">
              <div className="w-10 h-10 rounded-full bg-gradient-to-br from-transit-500 to-severity-info flex items-center justify-center text-white font-bold">
                AG
              </div>
              <div>
                <p className="text-white font-medium">Ayush Gawai</p>
                <p className="text-xs text-dark-muted">MSDA Capstone Project</p>
              </div>
            </div>
            <div className="h-8 w-px bg-dark-border hidden md:block"></div>
            <div className="text-xs text-dark-muted">
              <p>San Jos State University</p>
              <p>Department of Applied Data Science</p>
            </div>
          </div>
          <div className="text-xs text-dark-muted text-center md:text-right">
            <p> Data from: <span className="text-transit-500">Local Database (SQLite)</span></p>
            <p> Source: GTFS Feeds (BART, VTA)</p>
            <p> Location: {cityInfo || 'San Francisco Bay Area, San Jose'}</p>
            <p className="mt-1 text-[10px]"> 2024 Ayush Gawai. All rights reserved.</p>
          </div>
        </div>
      </div>
    </div>
  )
}
